<template>
  <div>
    <input type="file" ref="input" class="d-none" :fieldName="fieldName" :value="imgFile" accept="image/png, image/jpeg, image/gif, image/jpg" @change="uploadImg($event, 1)">
    <v-dialog :value="option.img" max-width="400" persistent>
      <v-card>
        <div class="cropper" style="height:400px">
          <vueCropper
            ref="cropper"
            :img="option.img"
            :outputSize="option.size"
            :outputType="option.outputType"
            :info="option.info"
            :full="option.full"
            :canMove="option.canMove"
            :canMoveBox="option.canMoveBox"
            :original="option.original"
            :autoCrop="option.autoCrop"
            :fixed="option.fixed"
            :fixedNumber="option.fixedNumber"
            :centerBox="option.centerBox"
            :infoTrue="option.infoTrue"
            :fixedBox="option.fixedBox"
            :autoCropWidth="option.autoCropWidth"
            :autoCropHeight="option.autoCropHeight"
          ></vueCropper>
        </div>

        <v-card-actions>
          <v-btn icon @click="rotateLeft">
            <v-icon>mdi-rotate-left</v-icon>
          </v-btn>
          <v-btn icon @click="rotateRight">
            <v-icon>mdi-rotate-right</v-icon>
          </v-btn>
          <v-btn icon @click="pickImg">
            <v-icon>mdi-image</v-icon>
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn color="gray" @click="destroy">
            
          </v-btn>
          <v-btn color="success" @click="finish('blob')">
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { VueCropper } from 'vue-cropper'
export default {
  props: {
    trigger: {
      type: Element
    },
    fieldName: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      headImg: '',
      crap: false,
      previews: {},
      dialog: false,
      option: {
        img: '', // 
        info: true, // 
        outputSize: 0.8, // 
        outputType: 'jpeg', // 
        canScale: true, // 
        autoCrop: true, // 
        fixedBox: true, //  
        fixed: true, // 
        fixedNumber: [1, 1], // 
        full: false, // 
        canMoveBox: false, // 
        original: true, // 
        centerBox: true, // 
        infoTrue: true, // true  false 
        heigh: false,
        mode: '100%',
        autoCropWidth: '400',
        autoCropHeight: '400'
      },
      fileName: '',
      downImg: '#',
      imgFile: '',
      uploadImgRelaPath: ''
    }
  },
  components: {
    VueCropper
  },
  methods: {
    rotateLeft () {
      this.$refs.cropper.rotateLeft()
    },
    rotateRight () {
      this.$refs.cropper.rotateRight()
    },
    destroy () {
      this.$refs.cropper.clearCrop()
      this.option.img = ''
    },
    pickImg () {
      this.$refs.input.click()
    },
    finish () {
      let formData = new FormData()
      this.$refs.cropper.getCropBlob((data) => {
        let img = window.URL.createObjectURL(data)
        this.model = true
        this.modelSrc = img
        formData.append('avatar', data, this.fileName)
        this.$emit('submit', formData)
        this.destroy()
      })
    },
    uploadImg (e, num) {
      this.fileName = 'avatar.jpg'
      if (!/\.(jpg|jpeg|png|JPG|JPEG|PNG)$/.test(e.target.value)) {
        alert('.gif,jpeg,jpg,png,bmp')
        return false
      }
      var reader = new FileReader()
      reader.onload = (e) => {
        this.option.img = window.URL.createObjectURL(new Blob([e.target.result]))
      }
      reader.readAsArrayBuffer(e.target.files[0])
    }
  },
  mounted () {
    if (this.trigger) this.trigger.addEventListener('click', this.pickImg)
  }
}
</script>

<style lang="scss">
.cropper {
    width: 400px;
    height: 400px;
    margin: 0 auto;
}
.vue-cropper {
  background-repeat: repeat;
}
</style>
